:root{
  --speed: 500ms;
  --text-color: black;
  --icon-size: 50px;
  --transformX-offset: calc(var(--icon-size)/-2);
  --menuColor: #fff;
  --dropdownWidth: 30vw;
  --borderRadius: 5px;
  font-family: Raleway, sans-serif;
}

@keyframes spawnLR{
    0%{
      opacity: 0%;
      width: 0vw;
      margin-left: 25vw;
    }
    100%{
      width: 50vw;
      opacity: 100%;
      margin-left: 0vw;
    }
  }

ul{
  list-style: none;
  margin: 0;
  padding: 0;
}

a{
  color: var(--text-color);
  text-decoration:none;
}

.App{
  width: 100vw;
  height: 100vh;
  display: grid;
  overflow:hidden;
  grid-template-columns: 1fr 2fr 2fr 2fr 2fr 2fr 2fr 2fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  pointer-events: none;
}

.menuOverlayContainer{
  position: relative;
  grid-column: 1/10;
  grid-row: 1/9;
  z-index: 0;
  display: grid;
  overflow:hidden;
  grid-template-columns: 1fr 10fr 1fr;
  grid-template-rows: 1fr 9fr 1fr 1fr;
  pointer-events: none;
  margin: 5px 5px 5px 5px;
}

.menuOverlayContainer>*{
  min-width: none;
  min-height: none;
  max-width: inherit;
  max-height: inherit;
  overflow: visible;

}


.menuIconButton{
  width: 100%;
  position: relative;
  

  text-align: left;
  align-items: center;
}






#bottomRowMenuContainer{
  grid-column: 1/4;
  grid-row: 4/5;
  text-align: center;
  align-content: center;
  align-items: center;
  pointer-events: none;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;

}



.viewMarkersButtonsDiv{
  pointer-events: auto;

}

.dropMarkerButton{
  pointer-events: auto;
}



.expandableMenu{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  pointer-events: auto;
}



.selfExpandableMenu{
  pointer-events: auto;
  position: relative;
}

.dropdown{
  position: relative;
  pointer-events: none;
  border-radius: var(--borderRadius);
  border: solid var(--menuColor);
  
  background-color: transparent;
  width: var(--dropdownWidth);
  z-index: 10;

  overflow: visible;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.dropdown>*{
  width: 100%;
}



.spawnLR{
  display: flex;
  pointer-events: none;
  position: absolute;
  justify-content: space-evenly;
  overflow-x: visible;
  overflow-y: visible;
  flex-direction: row;
  width: 0%;
  height: 100%;
  animation: spawnLR var(--speed) ease-in-out alternate both;
}

.selfExpanderItem{
  pointer-events: auto;
  position: relative;
  height: 100%;
  display: block;
  overflow-y: visible;
  align-items: center;
  justify-content: right;
  
  text-align: left;
  width: var(--icon-size)
}


.selfExpandClosed{
  margin-left: 50%;
  transform: translateX(calc(var(--icon-size)/-2));
}

.dropdownReverse>*{
  transform: translateX(var(--icon-size));
}

.dropdownRight{
  top: 0px;
  transform: translateX(100%);
}

.dropdownLeft{
  top: 0px;
  text-align: right;
  transform: translateX(-100%);
  right: calc(-1 * var(--icon-size));
}

.dropdownDown{
  top: 0px;
}

.dropdownUp{
  top: var(--icon-size)*-1;
}

.dropdownItem{
  height: var(--icon-size);
  pointer-events: auto;
  -webkit-user-drag: none;
  width: 100%;
  background-color: var(--menuColor);
  border-radius: var(--borderRadius);
  color: rgb(0, 0, 0);
  text-align: inherit;
  align-content: center;
  display: flex;
  flex-direction: row;
  position: relative;
  font-size: large;
  line-height: var(--icon-size);
  vertical-align: middle;

}

.sliderComponent{
  height: 100%;
  width: 100%;
  line-height: normal;
  font-size: small;
  text-align: left;
  justify-content: space-evenly;
  display: inline-flex;
  vertical-align: top;
  align-items: center;
  

}




#mapWrapper{
  position: absolute;
  z-index: 0;
  height: 100%;
  width: 100%;
  grid-column: 2/9;
  grid-row: 1/9;
  pointer-events: auto;
}

#dropMarkerButton{
  grid-column: 3/4;
  grid-row: 7/8;
}

#deleteMarkerButton{
  grid-column: 4/5;
  grid-row: 7/8;
  text-align: right;
  align-items: right;
}

#accountButton{
  position: absolute;
  grid-column: 3/4;
  grid-row: 1/2;
  max-width: var(--dropdownWidth);
  justify-content: right;
  align-items: right;
  text-align: right;
}

#settingsButton{
  position:absolute;
  grid-column: 1/2;
  grid-row: 1/2;
  max-width: var(--dropdownWidth);

}

.burgerMenu{
  height: var(--icon-size);
  width: var(--icon-size);
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  box-sizing: border-box;
}

.burgerMenu__line{
  width: 80%;
  height: 5px;
  background-color: black;
  border: solid black;
  border-width: 0px;
  transform: rotate(0deg);
  transition: border-width 1s ease-in-out 0s;
  

  
  
}

.burgerMenu:hover>.burgerMenu__line{
  border-width: 2px;
}



.selectMarkerTypeDiv{
  position:absolute;
  grid-column: 1/4;
  grid-row: 3/3;
  pointer-events: auto;
  display: grid;
  grid-template-columns: repeat(3,1fr);
}
.selectMarkerTypeDiv>*{
  position:relative;
  width: 50vw;
  align-items: center;
  justify-content: center;
  text-align: center;
}

#selectMarkerTypeButton{
  grid-column: 2/3;

}

.markerVisual {
  position: inherit;
  border-radius: 50%;
  border: 8px solid;
  width: 8px;
  height: 8px;
  right: 13px;
  top: 7px;
}

.markerVisual::after {
  position: inherit;
  content: '';
  width: 0px;
  height: 0px;
  bottom: -30px;
  left: -6px;
  border: 10px solid transparent;
  border-top: 17px solid;
}

.markerVisualBG {
  border-radius: 50%;
  position: absolute;
  width: 50px;
  height: 50px;
}

.landingpage {
  justify-content: center;
  color: white;
  font-size: 150px;
  font-family:Verdana, Geneva, Tahoma, sans-serif;
}



.landingpage .loginpage {
  display: flex;
 
}

.loginButton{
  font-size: 80px;
  background-color: white;
  border-radius: 10px;
  width: auto;
  height: auto;
  font-family:Verdana, Geneva, Tahoma, sans-serif;

}
.loginButton :hover{
  background-color: gray;

}

.App {
  background: none;
}








.mapCustomControl{
  background-color: "#fff";
  border: "2px solid #fff";
  border-radius: "3px";
  box-shadow: "0 2px 6px rgba(0,0,0,.3)";
  color: "rgb(25,25,25)";
  cursor: "pointer";
  font-family: "Roboto,Arial,sans-serif";
  font-size: "16px";
  line-height: "38px";
  margin: "8px 0 22px";
  padding: "0 5px";
  text-align: "center";
}